.side-nav{
    background-color: $white;
    position: fixed;
    top: 0;
    right: 0;
    width: 0;
    height: 100%;
    @include d-flexbox(column);
    justify-content: space-evenly;
    overflow: hidden;
    box-shadow: -5px 0 20px -5px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    transition: .2s;
    img{
        position: fixed;
        top: 30px;
        right: 30px;
        display: none;
        cursor: pointer;
    }
    nav{
        width: 100%;
        ul{
            list-style: none;
            li{
                padding: 25px;
                a{
                    color: black;
                }
            }
        }
    }
}