.site-wrapper {
  header {
    display: inline; // support for position sticky
    .top-header {
      @include d-flexbox(row);
      justify-content: space-between;
      background-color: #ccc;
      height: 40px;
      padding: 15px 5vw;
      .left-header {
        @include d-flexbox(row);
        .circle {
          background-color: black;
          width: 1.5rem;
          height: 1.5rem;
          border-radius: 50%;
          @include d-flexbox(row);
          justify-content: center;
          margin-right: 0.25rem;
          i {
            color: white;
          }
        }
      }
      .right-header {
        @include d-flexbox(row);
        div {
          margin-left: 0.75rem;
        }
        .phone {
          @include d-flexbox(row);
        }
        .email {
          @include d-flexbox(row);
        }
      }
    }
    .top-navbar {
      position: sticky;
      top: 0;
      z-index: 999;
      @include d-flexbox(row);
      justify-content: space-between;
      height: 100px;
      background-color: $white;
      padding: 15px 5vw;
      box-sizing: border-box;
      box-shadow: 4px 0 20px -5px rgba(0, 0, 0, 0.2);
      .left-nav {
        font-size: 2.3em;
        font-weight: 900;
        span {
          display: inline-block;
          width: 0.7rem;
          height: 0.7rem;
          background-color: $primary;
          margin-left: -5px;
        }
      }
      .right-nav {
        @include d-flexbox(row);
        nav {
          ul {
              @include d-flexbox(row);
              list-style: none;
            li {
                padding: 10px;
              a {
                color: black;
                font-size: 1.25em;
                display: block;
              }
            }
          }
        }
        img{
            cursor: pointer;
            display: none;
        }
      }
      
    }
  }
}

@media only screen and (max-width: 991.98px) {
  .site-wrapper {
    header {
      .top-header {
        .right-header {
          .phone,
          .email {
            div {
              display: none;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1199.98px) {
    .site-wrapper {
      header {
        .top-navbar{
            .right-nav{
                nav{
                    display: none;
                }
                img{
                    display: block!important;
                }
            }
        }
      }
    }
}