.products{
    background-color: $white;
    @include d-flexbox(column);
    padding-top: 100px; 
    text-align: center;
    .products-list{
        display: flex;
        flex-wrap: wrap;
        margin-left: -15px;
        margin-right: -15px;
        .col-4{
            flex: 1 0 30%;
            max-width: 30%;
            padding: 25px;
            box-sizing: border-box;
            .product-item{
                width:100%;
                img{
                    width: 100%;
                }
            }
        }
    }
}