main{
    .banner{
        max-width: 600px;
        height: 75vh;
        @include d-flexbox(column);
        justify-content: center;
        align-items: flex-start;
        font-size: 16px;
        h1{
            font-size: 5em;
            color: $white;
            margin-bottom: 0;
        }
        p{
            font-size: 1.3em;
            color: $white;
        }
        .btn-group{
            @include d-flexbox(row);
            width: 100%;
            a{
                margin-right: 5px;
                margin-bottom: 5px;
            }
        }
    }
}
@media only screen and (min-width: 1199.98px){
    main{
        .banner{
            max-width: 700px !important;
            font-size: 16px;
            p{
                font-size: 1.75em;
            }
        .btn-group{
            flex-direction: column !important;
            align-items: flex-start!important;
        }
    }
    }
}

@media only screen and (max-width: 991.98px){
    main{
        .banner{
            max-width: 450px !important;
            font-size: 13px;
            p{
                font-size: 1.5em;
            }
        .btn-group{
            flex-direction: column !important;
            align-items: flex-start!important;
        }
    }
    }
}

@media only screen and (max-width: 575.98px){
    main{
        .banner{
            max-width: 100% !important;
            font-size: 9px;
            p{
                font-size: 2em;
            }
        .btn-group{
            flex-direction: column !important;
            align-items: flex-start!important;
            a{
                width: 100%;
            }
        }
    }
    }
}